import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { GenerarPedidoComponent } from './generar-pedido/generar-pedido.component';
import { ListarPedidosComponent } from './listar-pedidos/listar-pedidos.component';
import { SeguimientoPedidosComponent } from './seguimiento-pedidos/seguimiento-pedidos.component';

const routes: Routes = [
  {
    path: 'generarPedido',
    component: GenerarPedidoComponent
  },
  {
    path: 'listadoPedido',
    component: ListarPedidosComponent
  },
  {
    path: 'seguimientoPedido',
    component: SeguimientoPedidosComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PedidosRoutingModule { }
