import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { PageEvent } from '@angular/material/paginator';
import { PaginatorFacadeService } from 'src/app/shared/paginator/paginator-facade.service';
import { ListarPedidosFacadeService } from './listar-pedidos-facade.service';

@Component({
  selector: 'app-listar-pedidos',
  templateUrl: './listar-pedidos.component.html',
  styleUrls: ['./listar-pedidos.component.css']
})
export class ListarPedidosComponent implements OnInit {

  public buscar = new FormControl('');
 //Paginacion
 public pageSize = 10;
 public page = 0;
 public pageEvent!: PageEvent;
 public pageIndex: number = 0;
 public desde = 0;
 public hasta = 10;
 public formPedido: FormGroup;
 public pedido:any;
 public detallePedido = new FormControl({value: '', disabled: true});
 public observacion = new FormControl({value: '', disabled: true});

  constructor(public listarPedidosFacade:ListarPedidosFacadeService, public paginatorService:PaginatorFacadeService, public dialog: MatDialog) {
    this.listarPedidosFacade.MostrarPedidos('0');
  }

  ngOnInit(): void {
  }

  next(event: PageEvent) {
    if (event.pageIndex === this.pageIndex + 1) {
      this.desde = this.desde + this.pageSize;
      this.hasta = this.hasta + this.pageSize;
    } else if (event.pageIndex === this.pageIndex - 1) {
      this.desde = this.desde - this.pageSize;
      this.hasta = this.hasta - this.pageSize;
    }
    this.pageIndex = event.pageIndex;
  }

  openDialog(template, pedido?) {
    this.pedido = pedido;
    this.detallePedido.setValue(this.pedido.DetallePedido || '');
    this.observacion.setValue(this.pedido.Observacion || '');

    const dialogRef = this.dialog.open(template, {
      panelClass: "app-full-bleed-dialog", //Agregar una clase ccs al dialogo
      width: '90%',
      disableClose: true
    });
    
  }

  descargarImagen(){
    window.open(this.pedido.Url);
  }
}
