import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { ToastrServiceLocal } from 'src/app/services/toast/toastr.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class ListarPedidosFacadeService {

  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private Pedidos$ = new BehaviorSubject<any[]>([]);
  public responsePedidos$: Observable<any[]> = this.Pedidos$.asObservable();

  constructor(public dataApi:DataApiService, public toast:ToastrServiceLocal, public _mensajesHttp:MensajesHttpService ) { }

  MostrarPedidos(params: any) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.GetDataApi(`pedido/pedido/usuario/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.Pedidos$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los pedidos', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }
}
