import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { ToastrServiceLocal } from 'src/app/services/toast/toastr.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class SeguimientoPedidoFacadeService {

  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private Pedidos$ = new BehaviorSubject<any[]>([]);
  public responsePedidos$: Observable<any[]> = this.Pedidos$.asObservable();

  private Reparto$ = new BehaviorSubject<any[]>([]);
  public responseReparto$: Observable<any[]> = this.Reparto$.asObservable();

  private EstadoProceso$ = new BehaviorSubject<any[]>([]);
  public responseEstadoProceso$: Observable<any[]> = this.EstadoProceso$.asObservable();

  constructor(public dataApi:DataApiService, public toast:ToastrServiceLocal, public _mensajesHttp:MensajesHttpService ) { }

  MostrarSeguimientoPedidos(params: any) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.GetDataApi(`pedido/pedidoSeguimiento`, params).pipe(
      tap((result: DataApi | any) => {
  
        this.Cargando$.next(false);
        this.Pedidos$.next(result.data);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los pedidos', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarEstadosProceso(params: any) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/mostrarEstadoProceso`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.EstadoProceso$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los pedidos', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarPedido(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PutDataApi(`pedido/pedido/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el pedido', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarEstadoPedidos(params: any, callback) {
    this.Cargando$.next(true);
    this.Pedidos$.next([]);
    const request$ = this.dataApi.PutDataApi(`pedido/pedido/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        callback(result);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Pedidos$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar los pedidos', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarReparto(params: any) {
    this.Cargando$.next(true);
    this.Reparto$.next([]);
    const request$ = this.dataApi.GetDataApi(`mantenimiento/reparto/`, params).pipe(
      tap((result: DataApi | any) => {

        this.Cargando$.next(false);
        this.Reparto$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Reparto$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar el reparto', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  agregarPedido(pedido){
    let data = this.Pedidos$.value;
    data.forEach((d)=>{
      if(d.Id === pedido.IdEstado){
        let newPedidos:any[] = d.pedidos.filter(p => p.Id != pedido.Id);
        newPedidos.push(pedido);
        d.pedidos = newPedidos;
      }
    });
   
    this.Pedidos$.next([]);
    this.Pedidos$.next(data);
  }
}
