import {
  Component,
  ElementRef,
  Inject,
  OnInit,
  TemplateRef,
  ViewChild,
} from "@angular/core";
import { FormControl, Validators } from "@angular/forms";
import {
  CdkDragDrop,
  moveItemInArray,
  transferArrayItem,
} from "@angular/cdk/drag-drop";
import { SeguimientoPedidoFacadeService } from "./seguimiento-pedido-facade.service";
import { MatBottomSheet } from "@angular/material/bottom-sheet";
import { DOCUMENT } from "@angular/common";
import { interval, Subscription } from "rxjs";
import { ClientSocketService } from "src/app/services/client-socket.service";
import { ToastrServiceLocal } from "src/app/services/toast/toastr.service";
import { MatDialog } from "@angular/material/dialog";
import { ExportarFacadeService } from "src/app/services/utils/exportar-facade.service";
@Component({
  selector: "app-seguimiento-pedidos",
  templateUrl: "./seguimiento-pedidos.component.html",
  styleUrls: ["./seguimiento-pedidos.component.scss"],
})
export class SeguimientoPedidosComponent implements OnInit {
  @ViewChild('modalActualizarPedido', { static: true }) modalActualizarPedido: TemplateRef<any>;
  @ViewChild('modalReparto', { static: true }) modalRepartidor: TemplateRef<any>;


  public elem;
  public fullscreen: boolean = false;
  prueba1 = ["1", "2", "3", "4"];

  prueba2 = ["5", "6", "7", "8", "9"];

  prueba3 = ["10", "11", "12", "13", "14"];

  prueba4 = ["15", "16", "17", "18", "19"];

  public encabezados: any[] = [
    "Aperturado",
    "Preparacin",
    "Empacando",
    "Despachado",
    "Entregado",
    "Rechazado",
  ];

  public buscar = new FormControl("");
  public pedido: any;
  public suscripciones: Subscription;

  constructor(
    public seguiminetoPedidos: SeguimientoPedidoFacadeService,
    private _bottomSheet: MatBottomSheet,
    @Inject(DOCUMENT) private document: any,
    public clienteSocket: ClientSocketService,
    public toast: ToastrServiceLocal,
    public dialog: MatDialog
  ) {
    this.seguiminetoPedidos.MostrarSeguimientoPedidos("");
    this.seguiminetoPedidos.MostrarEstadosProceso("");

    this.clienteSocket.listen("pedido").subscribe((result) => {
      this.notificarPedido(result);
    });
  }

  ngOnInit(): void {
    this.elem = document.documentElement;
  }

  drop(event: CdkDragDrop<string[]>) {
    if (event.previousContainer === event.container) {
      moveItemInArray(
        event.container.data,
        event.previousIndex,
        event.currentIndex
      );
    } else {
      transferArrayItem(
        event.previousContainer.data,
        event.container.data,
        event.previousIndex,
        event.currentIndex
      );
    }
  }

  openBottomSheet(template, pedido): void {
    this.pedido = pedido;
    this._bottomSheet.open(template);
  }

  cambiarEstado(params) {
    if(this.pedido.IdEstado === params.id){
      this.toast.mensajeWarning('','El pedido ya se encuenta en estado' + `${this.pedido.EstadoProceso}`);

    }else{
      this.pedido.idEstado = params.id;
      if(params.requiereObservacion){
        this.modalPedido();
      }else if(this.pedido.idEstado === 5){
        this.openModalRepartidor();
      }
      else{
        this.seguiminetoPedidos.ActualizarEstadoPedidos(this.pedido, (result) => {
          this.seguiminetoPedidos.MostrarSeguimientoPedidos("");
          this._bottomSheet.dismiss();
        });
      }
     
    }
   
  }

  openFullscreen() {
    if (this.elem.requestFullscreen) {
      this.elem.requestFullscreen();
    } else if (this.elem.mozRequestFullScreen) {
      /* Firefox */
      this.elem.mozRequestFullScreen();
    } else if (this.elem.webkitRequestFullscreen) {
      /* Chrome, Safari and Opera */
      this.elem.webkitRequestFullscreen();
    } else if (this.elem.msRequestFullscreen) {
      /* IE/Edge */
      this.elem.msRequestFullscreen();
    }
    this.fullscreen = true;
  }

  /* Close fullscreen */
  closeFullscreen() {
    if (this.document.exitFullscreen) {
      this.document.exitFullscreen();
    } else if (this.document.mozCancelFullScreen) {
      /* Firefox */
      this.document.mozCancelFullScreen();
    } else if (this.document.webkitExitFullscreen) {
      /* Chrome, Safari and Opera */
      this.document.webkitExitFullscreen();
    } else if (this.document.msExitFullscreen) {
      /* IE/Edge */
      this.document.msExitFullscreen();
    }
    this.fullscreen = false;
  }

  notificarPedido(pedido?) {

    this.toast.clearToasts();
    this.seguiminetoPedidos.agregarPedido(pedido);
    let audio = new Audio("./assets/audio/timbretimbrecasa.mp3");
    audio.play();
 

    this.toast.mensajeInfo("Nuevo Pedido", `#${pedido.Id}`);
    
  }

  public detallePedido = new FormControl('', [Validators.required]);
  public observacion = new FormControl('', [Validators.required]);

  verDetalle(template){
    this.detallePedido.setValue(this.pedido.DetallePedido || '');
    this.observacion.setValue(this.pedido.Observacion || '');

    let dialog = this.dialog.open(template, {
      width: "80%",
      panelClass: "app-full-bleed-dialog",
    });

    this._bottomSheet.dismiss();
  }

  
  modalPedido(){
    this.detallePedido.setValue(this.pedido.DetallePedido || '');
    this.observacion.setValue(this.pedido.Observacion || '');

    let dialog = this.dialog.open(this.modalActualizarPedido, {
      width: "80%",
      panelClass: "app-full-bleed-dialog",
    });

    this._bottomSheet.dismiss();
  }

  actualizarPedido(){

    if(this.observacion.invalid){
      this.toast.mensajeWarning("","Es requerido ingresar la observacin");
      return;
    }

    this.pedido.DetallePedido = this.detallePedido.value;
    this.pedido.Observacion = this.observacion.value;

    this.seguiminetoPedidos.ActualizarEstadoPedidos(this.pedido, (result) => {
      this.seguiminetoPedidos.MostrarSeguimientoPedidos("");
      this._bottomSheet.dismiss();
      this.dialog.closeAll();
    });

  }
  public idReparto = new FormControl('', [Validators.required]);

  openModalRepartidor(){
    this.idReparto.setValue('');
    this.seguiminetoPedidos.MostrarReparto('0');

    let dialog = this.dialog.open(this.modalRepartidor, {
      width: "80vw",
      height: "80vh",
      panelClass: "app-full-bleed-dialog",
    });

    this._bottomSheet.dismiss();
    
  }

  actualizarRepartidor(){
    if(this.idReparto.invalid){
      this.toast.mensajeWarning('','Es requerido seleccionar un repartidor');
      this.idReparto.markAllAsTouched();
      return;
    }
    this.pedido.IdReparto = this.idReparto.value;

    this.seguiminetoPedidos.ActualizarEstadoPedidos(this.pedido, (result) => {
      if(result.hasError === false){
        this.seguiminetoPedidos.MostrarSeguimientoPedidos("");
        this._bottomSheet.dismiss();
        this.dialog.closeAll();
      }
    });
  }

  descargarImagen(){
    window.open(this.pedido.Url);
  }
}
